@import url("https://fonts.googleapis.com/css2?family=Kaushan+Script&display=swap");
@import url("https://fonts.googleapis.com/css?family=Montserrat:300,regular,500,600,700,300italic,italic,500italic,600italic,700italic&display=swap");
* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

a:focus,
a:active {
  outline: none;
}

a {
  color: inherit;
  font-family: inherit;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
textarea {
  font-family: inherit;
  color: inherit;
  background: transparent;
  font-size: inherit;
  width: 100%;
  box-sizing: border-box;
}

input::-ms-clear {
  display: none;
}

button {
  color: inherit;
  font-family: inherit;
  background: transparent;
  box-sizing: border-box;
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
  height: auto;
  max-width: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: inherit;
}

.icon-inner {
  display: none;
}
@media (max-width: 47.99875em) {
  .icon-inner {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 2.1875rem;
    height: 1.875rem;
    border: 0.125rem solid #2e4d7e;
    border-radius: 0.1875rem;
    transition: 0.3s ease-in-out;
    z-index: 7;
  }
}

.icon-menu {
  display: none;
}
@media (max-width: 47.99875em) {
  .icon-menu {
    position: relative;
    display: block;
    flex: 0 0 1.875rem;
    width: 1.875rem;
    height: 1.125rem;
    cursor: pointer;
    z-index: 7;
  }
  .icon-menu span {
    transition: all 0.3s ease 0s;
    top: 0.5rem;
    right: 0;
    position: absolute;
    width: 100%;
    height: 0.125rem;
    background-color: #2e4d7e;
  }
  .icon-menu span:nth-child(1) {
    top: 0;
  }
  .icon-menu span:nth-child(2) {
    width: 75%;
  }
  .icon-menu span:nth-child(3) {
    top: auto;
    bottom: 0;
    width: 50%;
  }
  .menu-open .icon-menu span {
    background-color: #2e4d7e;
  }
  .menu-open .icon-menu span {
    transform: scale(0);
  }
  .menu-open .icon-menu span:first-child {
    transform: rotate(-45deg);
    top: 0.5rem;
  }
  .menu-open .icon-menu span:last-child {
    transform: rotate(45deg);
    bottom: 0.5rem;
    width: 100%;
  }
}

@media (max-width: 47.99875em) {
  .menu {
    text-align: center;
    position: fixed;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    padding-top: 7.5rem;
    overflow: auto;
    background: rgba(0, 0, 0, 0.7);
    transition: 0.5s;
    opacity: 0;
    visibility: hidden;
    z-index: 4;
  }
  .menu-open .menu {
    opacity: 1;
    visibility: visible;
  }
  .menu-open .menu::after {
    background: rgba(192, 192, 192, 0.9);
  }
  .menu:after {
    content: "";
    position: fixed;
    background: rgba(192, 192, 192, 0);
    top: 0;
    left: 0;
    width: 100%;
    height: 4.375rem;
    transition: 0.3s ease-in-out;
    z-index: 5;
  }
}

.marg {
  margin-bottom: 0.9375rem;
}
@media (max-width: 43.75em) {
  .marg {
    margin-bottom: calc(0.625rem + 5 * ((100vw - 20rem) / 380));
  }
}

.row {
  display: flex;
  align-items: center;
}

.center, .convert__btns {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 0.9375rem;
}

.gap {
  display: flex;
  align-items: center;
  gap: 0.9375rem;
}
@media (max-width: 43.75em) {
  .gap {
    gap: calc(0.3125rem + 10 * ((100vw - 20rem) / 380));
  }
}

.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  min-height: 2.1875rem;
  width: 100%;
  font-size: 1.125rem;
  font-weight: 500;
  color: #528be6;
  border: 0.125rem solid #528be6;
  border-radius: 1.875rem;
  padding: 0rem 0.625rem;
  transition: 0.3s ease-in-out;
}
@media (max-width: 43.75em) {
  .btn {
    font-size: calc(1rem + 2 * ((100vw - 20rem) / 380));
  }
}
.btn:hover {
  color: rgb(61, 204, 14);
  border: 0.0625rem solid rgb(61, 204, 14);
  background-color: rgb(43, 42, 51);
}
.btn:focus {
  box-shadow: 0rem 0rem 0.9375rem rgb(31, 243, 27);
  background-color: rgb(43, 42, 51);
  color: #ffa500;
}

.input {
  min-height: 1.875rem;
  padding: 0 0.625rem;
  border: 0.0625rem solid orange;
  transition: 0.3s ease-in-out;
}
.input::-moz-placeholder {
  font-size: 0.875rem;
  font-weight: 300;
  font-style: italic;
}
.input:-ms-input-placeholder {
  font-size: 0.875rem;
  font-weight: 300;
  font-style: italic;
}
.input::placeholder {
  font-size: 0.875rem;
  font-weight: 300;
  font-style: italic;
}
.input:focus {
  box-shadow: 0rem 0rem 0.9375rem rgb(31, 243, 27);
}

.img {
  display: block;
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}

.descr {
  font-size: 1rem;
  font-weight: 400;
  font-style: italic;
  color: #528be6;
  transition: 0.3s ease-in-out;
}
@media (max-width: 43.75em) {
  .descr {
    font-size: calc(0.875rem + 2 * ((100vw - 20rem) / 380));
  }
}

.title {
  text-align: center;
  font-size: 1.875rem;
  font-weight: 500;
  margin-bottom: 0.9375rem;
}
@media (max-width: 43.75em) {
  .title {
    font-size: calc(1.5rem + 6 * ((100vw - 20rem) / 380));
  }
}
.title span {
  color: orange;
  background: linear-gradient(45deg, orange, orangered, rgb(8, 244, 8), rgb(247, 10, 89), fuchsia, blue, rgb(29, 108, 234), aqua, green, rgb(255, 0, 43), rgb(30, 124, 217));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.subtitle {
  font-size: 1.5rem;
  font-weight: 500;
  font-style: italic;
  color: #528be6;
  margin-bottom: 0.4166666667em;
  transition: 0.3s ease-in-out;
}
@media (max-width: 43.75em) {
  .subtitle {
    font-size: calc(1.125rem + 6 * ((100vw - 20rem) / 380));
  }
}

.after {
  position: relative;
  padding-bottom: 0.1875rem;
}
@media (any-hover: hover) {
  .after:hover::after {
    width: 100%;
    left: 0;
  }
}
.after::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  height: 0.125rem;
  width: 0;
  background-color: rgba(82, 139, 230, 0.95);
  transition: 0.3s ease-in-out;
}

.after-img {
  position: relative;
}
@media (any-hover: hover) {
  .after-img:hover::after {
    opacity: 0;
  }
}
.after-img::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(17, 167, 253, 0.3);
  transition: 0.3s ease-in-out;
}

.error {
  color: orangered;
  font-weight: 900;
  text-transform: uppercase;
}

.convert__title {
  margin-bottom: 0.9375rem;
}
.convert__box {
  position: relative;
  width: 100%;
  margin-bottom: 0.9375rem;
}
.convert__box.move::before, .convert__box.move::after {
  opacity: 1;
  visibility: visible;
}
.convert__box::after, .convert__box::before {
  content: "";
  position: absolute;
  top: -10px;
  left: -10px;
  width: calc(100% + 20px);
  height: calc(100% + 20px);
  background: linear-gradient(45deg, #0000ff, #ff0000, #00ff00, #ff00ff, #ffff00, #0000ff, #ff0000, #00ff00, #ff00ff, #ffff00);
  background-size: 300%;
  z-index: -1;
  -webkit-animation: move 10s ease-out infinite;
          animation: move 10s ease-out infinite;
  transition: 0.3s ease-in-out;
  opacity: 0;
  visibility: hidden;
}
.convert__box::after {
  content: "";
  filter: blur(10px);
}
.convert__text {
  min-height: 18.75rem;
  width: 100%;
  padding: 0.625rem;
  color: #ffa500;
  border: 0.0625rem solid #528be6;
  resize: vertical;
  background-color: rgba(43, 42, 51, 0.9);
}
.convert__btns {
  max-width: 28.125rem;
  margin: 0 auto;
  padding: 0.9375rem;
  border-radius: 1.875rem;
  border: 0.0625rem solid #528be6;
  background-color: rgb(63, 62, 73);
}
.convert__wrap {
  position: relative;
  width: 100%;
}
.convert__input {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  font-size: 0;
  cursor: pointer;
}
.convert__btn_orangered {
  color: orangered;
}
.convert__btn_orange {
  color: orange;
}
.convert__btn_blue {
  color: blue;
}
.convert__btn_green {
  color: green;
}

@-webkit-keyframes move {
  0% {
    background-position: 0 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0 50%;
  }
}

@keyframes move {
  0% {
    background-position: 0 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0 50%;
  }
}
._ibg {
  position: relative;
}
._ibg img {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  -o-object-fit: cover;
     object-fit: cover;
  -o-object-position: center;
     object-position: center;
}

.ibg {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
.ibg img {
  display: none;
}

body {
  position: relative;
  font-family: "Montserrat";
  color: #528be6;
  background: rgb(43, 42, 51);
  font-weight: 400;
}
.menu-open body {
  overflow: hidden;
  touch-action: none;
  transition: 0.3s ease-in-out;
}

html {
  font-size: 16px;
  scroll-behavior: smooth;
}

.wrapper {
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.container {
  max-width: 43.75rem;
  margin: 0.9375rem auto;
  padding: 0rem 0.9375rem;
  box-sizing: content-box;
}

*:focus {
  outline: 0.0625rem solid orange;
}

.color {
  color: #2b2a33;
}